@media screen and (max-width: 768px) {
  .Paddock .PageBanner__title {
    font-size: 57px;
    margin-bottom: -18px; } }

.Paddock .legenda-1 {
  position: absolute;
  top: 50px;
  right: -120px;
  max-width: 270px;
  text-align: left; }

@media screen and (max-width: 992px) {
  .Paddock .legenda-1 {
    left: unset;
    left: 50%;
    bottom: -10px;
    text-align: center;
    top: unset;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    padding: 15px;
    font-size: 14px;
    width: 280px; } }

.Paddock__btn {
  font-size: 17px;
  text-transform: uppercase;
  color: #9D8855;
  padding: 10px 60px;
  border: 1px solid #9D8855;
  -webkit-transition: all 0.5s;
  transition: all 0.5s; }

@media screen and (max-width: 992px) {
  .Paddock__btn {
    padding: 10px 40px; } }

.Paddock__btn:hover {
  background-color: #9D8855;
  color: #fff; }

.Paddock video {
  width: 100%; }
